<template>
  <div class="logo">
      <canvas id="canvas" :width="w" :height="h" @click="canvas"></canvas>
  </div>
</template>

<script>
export default {
  name: "logo",
  props: {

  },
  data() {
    return {
      w:window.innerWidth,
      h:window.innerHeight,
      objArr:[]
    }
  },
  methods: {
    canvas:function(e){
      console.log(e)
      this.init(e)
    },
    init:function(e){
      this.objArr = []
        for(var i=0;i<200;i++){
          setTimeout(()=>{
            var obj = new Object();
            obj.x = e.offsetX;
            obj.y = e.offsetY
            obj.speedX = Math.random()*2 - 1
            obj.speedY = Math.random()*2 - 1
            obj.a = Math.random()/10 - 0.03;
            obj.o = Math.random()
            obj.Cr = 0
            obj.Cg = 0
            obj.Cb = 0
            obj.pt = parseInt(Math.random()*10)
            this.objArr.push(obj)
          },50*i)
      }
    },
    draw:function (){
      var canvas = document.getElementById("canvas");   
      var ctx=canvas.getContext("2d");
      var this_ = this
      animat()

      function animat(){
        ctx.shadowColor="rgba(0,0,0,0.05)";
        ctx.shadowBlur=5;
        ctx.fillStyle = "rgba(0,0,0,0.05)"
        ctx.rect(0,0,this_.w,this_.h);
        ctx.fill();

        for(var i = 0;i<this_.objArr.length;i++){
          // console.log(objArr[i].o)
          ctx.fillStyle = "rgba("+this_.objArr[i].Cr+","+this_.objArr[i].Cg+","+this_.objArr[i].Cb+","+this_.objArr[i].o+")"
          if(this_.objArr[i].Cr<255){
            this_.objArr[i].Cr+=1
          }
          if(this_.objArr[i].Cg<255){
            this_.objArr[i].Cg+=4
          }

          if(this_.objArr[i].Cb<255){
            this_.objArr[i].Cb+=3
          }

          if(this_.objArr[i].Cb>=200&&this_.objArr[i].Cg>=200&&this_.objArr[i].Cr>=200){
            this_.objArr[i].Cg = 0
            this_.objArr[i].Cb = 0

          }
          ctx.beginPath()
          ctx.shadowColor="rgba("+this_.objArr[i].Cr+","+this_.objArr[i].Cg+","+this_.objArr[i].Cb+","+1+")"
          ctx.shadowBlur=15;
          ctx.shadowOffsetX=0;
          ctx.shadowOffsetY=0;
          if(this_.objArr[i].pt>=9){
            this_.objArr[i].Cr =150
            this_.objArr[i].Cg = 0
            this_.objArr[i].Cb = 0
          }
          ctx.arc(this_.objArr[i].x,this_.objArr[i].y,1.5,0,2*Math.PI)

          this_.objArr[i].x+=this_.objArr[i].speedX;
          this_.objArr[i].y+=this_.objArr[i].speedY;
          // this_.objArr[i].speedX+= this_.objArr[i].a
          this_.objArr[i].speedY-=this_.objArr[i].a
          // this_.objArr[i].o-=0.0001
          if(this_.objArr[i].speedY<=-2){
            this_.objArr[i].a=-Math.random()/10
          }
          if(this_.objArr[i].speedY>=2){
            this_.objArr[i].a=Math.random()/10
          }

          if(this_.objArr[i].x>this_.w || this_.objArr[i].x<0){
            this_.objArr[i].speedX = -this_.objArr[i].speedX
          }

          if(this_.objArr[i].y>this_.h||this_.objArr[i].y<0){
              this_.objArr[i].speedY = -this_.objArr[i].speedY

          }
          ctx.fill()
          ctx.closePath()
          
        }
        requestAnimationFrame(animat); 
      }
    }
  },
  mounted() {
    // requestAnimationFrame
    this.draw()
    window.onresize = () => {
      this.w = window.innerWidth
      this.h = window.innerHeight 
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
*{
    margin: 0;
    padding: 0;
}
.logo{
    height: 100vw;
    width:100vw;
}
#canvas{
    background:black;
}
</style>
