<template>
  <div class="imgCanvas">
      <canvas id="canvas" width="600" height="400"></canvas>
      <img src="../img/img02.jpg" alt="" width="600px" id="img01" style="display:none">
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  props: {
    
  },
  data() {
      return {
          imgStart:[100,100],
          w:600,
          h:400,
          imgwidth:400,
          imgHeight:200,
          pArr:[]
      }
  },
  methods:{
      draw:function (){
        var canvas = document.getElementById("canvas");   
        var ctx=canvas.getContext("2d");
        var image = document.getElementById("img01")
        var this_ = this
        image.onload = ()=>{
            ctx.drawImage(image,this.imgStart[0],this.imgStart[1],this.imgwidth,this.imgHeight);
            animat()
            var j=0,arr=[]
            for(var i=0;i<this.imgHeight/10;i++){
                var imgData=ctx.getImageData(this_.imgStart[0],this_.imgStart[1]+(i*10),1,1)
                arr[i] = imgData
                setTimeout(()=>{
                    var obj = new Object()
                    obj.r = arr[j].data[0]
                    obj.g = arr[j].data[1]
                    obj.b = arr[j].data[2]
                    obj.o = 1
                    obj.x = 100
                    obj.y = this_.imgStart[1]+(j*10)
                    obj.speedX = -3
                    obj.speedY = -3
                    obj.aX = 0.1
                    obj.aY = 0.15

                    if(j>=10){
                        obj.speedX = -3
                        obj.speedY = 3
                        obj.aX = 0.1
                        obj.aY = -0.15
                    }
                    this_.pArr.push(obj)
                    j++;
                },20*i)
            }

        }

        // var x = 100 , y = 100 ,speedX = -3,speedY = -3,aX = 0.1,aY = 0.15
        
        function animat(){
            ctx.shadowColor="none"
            ctx.shadowBlur=0;
            // ctx.drawImage(image,this_.imgStart[0],this_.imgStart[1],400,200);

            ctx.fillStyle = "rgba(0,0,0,0.1)"
            ctx.rect(0,0,this_.w,this_.h);
            ctx.fill();

            for(var i=0;i<this_.pArr.length;i++){
                ctx.beginPath()
                ctx.shadowColor="rgba("+this_.pArr[i].r+5+","+this_.pArr[i].g+5+","+this_.pArr[i].b+5+","+this_.pArr[i].o+")"
                
                ctx.fillStyle = "rgba("+this_.pArr[i].r+","+this_.pArr[i].g+","+this_.pArr[i].b+","+this_.pArr[i].o+")"
                ctx.shadowBlur=2;
                ctx.shadowOffsetX=0;
                ctx.shadowOffsetY=0;
                this_.pArr[i].x += this_.pArr[i].speedX
                this_.pArr[i].y += this_.pArr[i].speedY
                
                this_.pArr[i].speedX+=this_.pArr[i].aX
                this_.pArr[i].speedY+=this_.pArr[i].aY
                
                this_.pArr[i].aX+=0.005
                if(this_.pArr[i].aX>=0.35){
                    this_.pArr[i].o = 0
                }
                ctx.arc(this_.pArr[i].x,this_.pArr[i].y,2,0,2*Math.PI)
                
                ctx.fill()
                ctx.closePath()
            }
            
            requestAnimationFrame(animat);   
        }
      }
  },
  mounted:function (){
      this.draw()
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    #canvas{
        background-color: black;
    }
    .imgCanvas{
        width: 600px;
        height: 400px;
        background-color: black;
    }
</style>
